'use client';

import Link from "next/link";
import { RadioTower, ArrowRight } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useLanguage } from "@/contexts/language-context";
import { translations } from "@/lib/translations";


function LoginForm() {
  const { language } = useLanguage();
  const t = translations[language].login;
  return (
    <>
      <div className="grid gap-4">
        <div className="grid gap-2">
          <Label htmlFor="email">{t.emailLabel}</Label>
          <Input
            id="email"
            type="email"
            placeholder="m@example.com"
            required
            defaultValue="admin@telepowerx.com"
          />
        </div>
        <div className="grid gap-2">
          <div className="flex items-center">
            <Label htmlFor="password">{t.passwordLabel}</Label>
            <Link
              href="#"
              className="ml-auto inline-block text-sm underline"
            >
              {t.forgotPassword}
            </Link>
          </div>
          <Input id="password" type="password" required defaultValue="password" />
        </div>
        <Button asChild type="submit" className="w-full">
          <Link href="/dashboard">{t.loginButton} <ArrowRight className="ml-2 h-4 w-4" /></Link>
        </Button>
      </div>
      <div className="mt-4 text-center text-sm">
        {t.signupPrompt}{" "}
        <Link href="#" className="underline">
          {t.signUp}
        </Link>
      </div>
    </>
  );
}


export default function LoginPage() {
  const { language, dir } = useLanguage();
  const t = translations[language].login;

  return (
    <div className="flex items-center justify-center min-h-screen bg-muted/40 p-4" dir={dir}>
      <Card className="mx-auto max-w-sm w-full shadow-2xl">
        <CardHeader className="text-center">
          <div className="inline-flex justify-center items-center mb-4">
            <RadioTower className="h-10 w-10 text-primary" />
          </div>
          <CardTitle className="text-3xl font-headline">{t.title}</CardTitle>
          <CardDescription>
            {t.description}
          </CardDescription>
        </CardHeader>
        <CardContent>
          <LoginForm />
        </CardContent>
      </Card>
    </div>
  );
}
